import { useEffect, useState } from 'react';
import {
  WithdrawListApi,
  WithdrawAllowApi,
  WithdrawAllowHandlApi,
  WithdrawDelApi,
} from 'utils/Api';
import { Table, Button, Popconfirm } from 'antd';
const { Column } = Table;

const Withdraw = () => {
  const [tableData, setTableData] = useState();

  useEffect(() => {
    WithdrawListApi().then((res) => {
      setTableData(
        res.data.map((item) => ({
          ...item,
          id: item._id.slice(0, 7),
          createdAt: item.createdAt.replace(/[TZ]/g, ' '),
          updatedAt: item.updatedAt.replace(/[TZ]/g, ' '),
        }))
      );
    });
  }, []);

  const allow = (record) => {
    WithdrawAllowApi(record).then((res) => {
      const swapData = tableData.filter((item) => item._id !== record._id);
      setTableData([...swapData, res.data]);
    });
  };
  const allowHandl = (record) => {
    WithdrawAllowHandlApi(record).then((res) => {
      const swapData = tableData.filter((item) => item._id !== record._id);
      setTableData([...swapData, res.data]);
    });
  };
  const del = (record) => {
    WithdrawDelApi(record._id).then((res) => {
      const swapData = tableData.filter((item) => item._id !== record._id);
      setTableData([...swapData, res.data]);
    });
  };

  return (
    <Table dataSource={tableData} rowKey="_id">
      <Column title="ID" dataIndex="id" />
      <Column title="会员手机" render={(_, record) => record.user.phone} />
      <Column title="提现金额" dataIndex="amount" />
      <Column title="手续费" dataIndex="fees" />
      <Column title="实发金额" dataIndex="blance" />
      <Column
        title="提现银行"
        render={(_, record) => record.bankInfo.bank.value}
      />
      <Column
        title="提现帐号"
        render={(_, record) => record.bankInfo.bankAccount}
      />
      <Column title="申请时间" dataIndex="createdAt" />
      <Column
        title="状态"
        render={(_, record) => {
          if (record.status === 0) {
            return '未处理';
          } else if (record.status === 1) {
            return '已提现';
          } else if (record.status === 2) {
            return '已取消';
          } else if (record.status === 3) {
            return '提现失败';
          } else if (record.status === 4) {
            return '处理中';
          }
        }}
      />
      <Column
        title="操作"
        dataIndex=""
        render={(_, record) =>
          record.status === 0 ? (
            <div className="flex gap-1">
              <Popconfirm
                title="确定是否允许提现申请"
                onConfirm={() => allow(record)}
                okType="link"
                cancelText="取消"
                okText="确认"
              >
                <Button type="primary" danger>
                  允许提现
                </Button>
              </Popconfirm>
              <Popconfirm
                title="确定是否手动提现成功"
                onConfirm={() => allowHandl(record)}
                okType="link"
                cancelText="取消"
                okText="确认"
              >
                <Button style={{ margin: '1rem' }} danger>
                  手动处理
                </Button>
              </Popconfirm>
              <Popconfirm
                title="确定是否取消提现申请"
                onConfirm={() => del(record)}
                okType="link"
                cancelText="取消"
                okText="确认"
              >
                <Button type="primary">取消提现</Button>
              </Popconfirm>
            </div>
          ) : (
            ''
          )
        }
      />
    </Table>
  );
};

export default Withdraw;
